import React from "react";

import Image from "../Image";

import {AUTHOR_MAP} from "../../author/authorInfo";
import "./styles.scss";

const Author = ({ id }) => {
    const author = AUTHOR_MAP[id] || null; 
    if (!author) {
        return null;
    }

    const name = author.name || "";
    const picture = author.picture || "";
    return (
        <div className="AuthorInfo">
            <div className="AuthorBubbleImageContainer">
                <Image className="AuthorBubble" src={picture} alt={name} />
            </div>
            <div className="AuthorTitle">By {name}</div>
        </div>
    );
};

export default Author;
