import React from 'react';
import { Link } from "gatsby";
import Image from "../../../components/Image";
import Author from "../../Author";

import "./styles.scss";

const HeadlineCard = ({ data }) => {
    const {frontmatter} = data;
    return (
        <Link className={`SmallHeadlineCard`} to={`/${frontmatter.slug}`}>
            <div className="imgContainer">
                <Image src={frontmatter.cover_photo} alt={frontmatter.title} />
            </div>

            <div className="descriptionContainer">
                <div className="title">
                    {frontmatter.title}
                </div>
                <div className="AuthorBubbleContainer">
                    <Author id={frontmatter.author} />
                </div>
                <div className="date">
                    {frontmatter.date}
                </div>
            </div>
        </Link>
    );
};

export default HeadlineCard;
