import React, {useEffect, useState} from "react";
import { StaticQuery, graphql } from 'gatsby';
import FeaturedBlogs from "../components/FeaturedBlogs";
import Layout from "../components/layout";
import ReactGA from 'react-ga';
// Components
import SEO from "../components/SEO";
import Headlines from "../components/Headlines";
import AdsInitializer from "../components/AdsInitializer";

import {createBlogPageMetadata} from "../jsonLd";

import "./styles.scss";

export default function Template() {
  const [start] = useState(new Date());
  useEffect(() => {
    return () => {
      const cur = new Date();
      const diff = cur.getMilliseconds() - start.getMilliseconds();
      ReactGA.timing({
        category: 'TimeOnHome',
        variable: 'time',
        value: diff, // in milliseconds
        label: "Home",
      });
    };
  });

  const adsData = [{
    id: "138821154", size: "728x90", id2: "138821154"
  }];

  return (
    <Layout adsData={adsData}>
      <StaticQuery
        query={graphql`
              query MyQuery {
                  recent: allMarkdownRemark(
                    limit: 15,
                    sort: { order: DESC, fields: [frontmatter___date] }
                  ) {
                    edges {
                        node {
                          id
                          excerpt
                          frontmatter {
                              author
                              slug
                              title
                              date(formatString: "MMMM D, YYYY")
                              cover_photo
                          }
                      }
                    }
                  }
              }
          ` }
        render={(data) => (
          <>
            <SEO
              title="TechWaifu - Most Recent Blog Posts"
              metadata={JSON.stringify(createBlogPageMetadata(data.recent.edges))}
            />
            <section className="Homepage">
              <Headlines data={data.recent.edges.slice(0, 5)} />
              <FeaturedBlogs hasAds={true} data={data.recent.edges.slice(5)} title="The Latest" tag="blogs/all" />
            </section>
          </>
        )} />
    </Layout >
  )
}
