import React from "react";

import BigHeadlineCard from "./BigCard";
import SmallHeadlineCard from "./SmallCard";

import "./styles.scss";

const Headlines = ({ data }) => {
    return (
        <section className="headlines">
                {data.map(({node} , idx) => {
                    if (idx === 0) {
                        return (
                            <BigHeadlineCard key={node.id} data={node} />
                        )
                    }
                    return (
                        <SmallHeadlineCard key={node.id} data={node} />
                    );
                })}
        </section>
    );
};

export default Headlines;

